.App {
  text-align: center;
  display: flex;
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-family: 'Montserrat', sans-serif;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  background-color: #256D85;
  justify-content: space-around;
  font-family: 'Montserrat', sans-serif;
  color: #06283D;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.homepagebg {
  background-color: rgba(0, 0, 0, 0.4); 

}

.background-image-blur {
  position: fixed;
  transition: "filter 0.75s ease-out";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./assets/bg2-sm.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.background-image {
  position: fixed;
  backdrop-filter: blur(5px);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*background-image: url('./assets/bg2.jpg');*/
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}


.logo {
  display: block;
  max-width:70px;
  max-height:400px;
  width: auto;
  height: auto;
}

.logo-text {
  color: white;
  font-family: 'Montserrat', serif;
  font-size: large;
  vertical-align: middle;
  margin: 0;
  padding-bottom: 20px;
  padding-top: 20px;
}

.top-logo-text {
  color: black;
  font-family: 'Montserrat', serif;
  font-size: large;
  vertical-align: middle;
  margin: 0;
  padding-bottom: 20px;
  padding-top: 20px;
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.body-content {
  padding-top: 60px;
  width: 100%;
  height: 100%;
  padding-right: 15pt;
  padding-left: 15pt;
}

.bottom-nav-text {
  color: white;
  font-family: Futura, serif;
}

.top-nav-text {
  color: black;
  font-family: Futura, serif;
}

ul.nav a:hover { 
  color: gray !important; 
}

*.unselectable {
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;

  /*
    Introduced in IE 10.
    See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
  */
  -ms-user-select: none;
  user-select: none;
}

.blur-load {
  background-size: cover;
  background-position: center;
}

.custom-toggler.navbar-toggler {
  border: none;
  color: transparent;
} 

.custom-dark-toggler.navbar-toggler {
  border: none;
  color: transparent;
}

.custom-toggler .navbar-toggler-icon { 
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 0.95)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.custom-dark-toggler .navbar-toggler-icon { 
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0, 0, 0, 0.95)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

@font-face {
  font-family: 'Mammoth';
  src: url('./assets/mammoth/Mammoth.otf') format('opentype');
}

.mammoth-font-xxlarge {
  font-family: 'Mammoth', sans-serif;
  color: black;
  font-size: xx-large;
}


.mammoth-font {
  font-family: 'Mammoth', sans-serif;
  color: black;
  font-size: small;
}

.centered-font {
  height: 100%;
  line-height: 100px;
  text-align: center;
  margin: 0;
}

.content {
  display: flex;
  flex: 1;
  padding: 10px;
  justify-content: center; /* Horizontally center the items */
  align-items: center; /* Vertically center the items */
  height: 100vh; /* Set the container's height (adjust as needed) */
  flex-direction: column;
}

.title-font-light {
  letter-spacing: .2rem;
  font-family: 'Poppins', sans-serif;
  color: white;
  font-size: 600%;
}

.subtitle-font-light {
  letter-spacing: .1rem;
  font-family: 'Poppins', sans-serif;
  color: white;
  font-size: 150%;
}

